import React from "react"
import Image1 from "../Images/aff.jpg"
import Image2 from "../Images/cics.jpg"
import Image3 from "../Images/msu.png"
import Image4 from "../Images/dsc.png"

function expcard(props) {
  const { expCompName, expCompLink, expLoc, expPeriod, expRole, expDuties, expId} = props
  var Image = Image1
  if (expId == 1){
    console.log(expId)
      Image = Image1
  }else if (expId == 2){
    console.log(expId)
      Image = Image2
  }else if (expId == 3){
    console.log(expId)
      Image = Image3
  }else if (expId == 4){
    console.log(expId)
      Image = Image4
  }
  return (
    <div class="row">
        <div className="col-lg-1"></div>
        <div className="col-lg-10">
            <div class="card mx-3 my-5">
            <div class="row" style={{padding: "15px"}}>
              <div className="col-lg-4" >
              <img class="card-img" src={Image} style={{height: "70px", width: "70px"}}/>
              <h6>{expCompName}</h6>
              <p>{expLoc}</p>
              <a href={expCompLink}>Official Site</a>
              </div>
              <div className="col-lg-8">
                <h6 style={{textAlign: "right"}}>{expPeriod}</h6>
                <h5 style={{textAlign: "left", color: "#FF8A00"}}>{expRole}</h5>
                <p class="card-text">{expDuties}</p>
              </div>
            </div>
            </div>  
        </div>
        <div className="col-lg-1"></div>
    </div>
  )
}

export default expcard
