import React from "react"
import "../style.css"
import Appbar from "../components/appbar"
import { graphql } from "gatsby"
import ExpCard from "../components/exp"
import { Helmet } from "react-helmet"

function Experience({ data }) {
  const expList = data.allExperienceYaml.edges

  return (
    <div className="container-fluid">
      <Appbar />
      <Helmet>
        <meta charSet="utf-8" />
        <title>Experience | Kunjal Panchal</title>
      </Helmet>
      <div className="row">
        {expList.map(({ node }) => {
          return (
            <ExpCard
              expCompName={node.companyname}
              expCompLink={node.companylink}
              expLoc={node.location}
              expPeriod={node.period}
              expRole={node.role}
              expDuties={node.duties}
              expId = {node.id}
            />
          )
        })}
      </div>
    </div>
  )
}

export default Experience

export const query = graphql`
  {
    allExperienceYaml {
      edges {
        node {
          companyname
          companylink
          location
          period
          role
          duties
          id
        }
      }
    }
  }
`
